@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 50px;
  padding-top: 70px;
  width: 90%;
}

.movies a {
  flex: 45% 1;
  text-decoration: none;
  color: inherit;
}

.movies .movie {
  width: 90%;
  background-color: white;
  margin-bottom: 70px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  color: #adaeb9;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}


/* .movies .movie a {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    text-decoration: none;
    color: inherit;
  } */
.movie img {
  position: relative;
  top: -50px;
  max-width: 180px;
  width: 100%;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .movie-title,
.movie .movie-year {
  margin: 0;
  font-weight: 300;
}

.movie .movie-title {
  margin-bottom: 5px;
  font-size: 20px;
  color: #2c2c2c;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 500;
}

.movie .movie-genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin: 5px 0px;
}

.movie-genres li,
.movie .movie-year {
  margin-right: 10px;
  font-size: 14px;
}


@media screen and (max-width:1280px) {
  .movies a {
    flex: 100% 1;
  }
  .movies .movie {
    width: 100%;

  }
}

body,
div,
ul,
li,
dl,
dt,
ol,
h1,
h2,
h3,
h4,
h5,
h6,
input,
fieldset,
legend,
p,
select,
table,
th,
td,
tr,
textarea,
button,
form {

    box-sizing: border-box;

}









html,
body,
#potato,
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
}


.about_container {
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
      0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    padding: 20px;
    border-radius: 5px;
    background-color: white;
    margin: 0 auto;
    margin-top: 100px;
    max-width: 50%;
    font-weight: 300;
  }
  
  .about_container span:first-child {
    font-size: 20px;
  }
  
  .about_container span:last-child {
    display: block;
    margin-top: 10px;
  }
.detail-wrap {
    max-width: 1080px;
    margin: 0 auto;
    display: flex;
    font-family: "roboto";

}

.detail-wrap>.detail-img {
    flex: 50% 1;
}

.detail-wrap>.detail-img img {
    max-width: 400px;
    height: 100%;
}

.detail-wrap>.detail-text {
    margin-left: 2vw;
}

.detail-wrap>.detail-text>.title {
    font-size: 2.5vw;
    font-family: 'Noto Sans KR', sans-serif;
    font-weight: 900;
}

.detail-wrap>.detail-text>.des {
    margin: 1vw 0;

}

.detail-wrap>.detail-text>.des span {
    margin-right: 1vw;
    color: #b8b8b8;
}
.nav {
    position: fixed;
    top: 50px;
    left: 10px;
    display: flex;
    flex-direction: column;
    background-color: white;
    padding: 10px 20px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
        0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    border-radius: 5px;
}

.nav a {
    text-decoration: none;
    color: #0008fc;
    text-transform: uppercase;
    font-size: 12px;
    text-align: center;
    font-weight: 600;
}

.nav a:not(:last-child) {
    margin-bottom: 20px;
}

